// 
export const USER_DATA = 'USER_DATA';
export default {
    state: {
        userData: {},   //
    },
    getters: {
      
    },
    mutations: {
        // request 
        [USER_DATA](state, data) {
            state.userData = data
        }
    },
    // 
    actions: {
        [USER_DATA]({ commit }, data) {
            commit(USER_DATA, data);
        }
    }
}