<template>
    <div class="user-my-mySetting">
        <div class="modify_pwd">
            <div class="pwdBox" @click="showPwd" ref="pwdBox">
                <img src="../../../assets/my/pwd.png"/>
                <span></span>
            </div>
           <el-popover
                placement="bottom"
                trigger="click"
                visible-arrow="true"
                v-model="visible_pwd">
                <div class="pwd_modifyBox">
                    <form>
                        <h6>*</h6>
                        <div class="phoneNum"><span class="text"></span><input v-model="pwdBox.phone.value" class="input" :class="{'pattern-style' : pwdBox.phone.pattern}" maxlength="11" placeholder="" /></div>
                        <div class="phoneCode"><span class="text"></span><input v-model="pwdBox.code.value" class="input" :class="{'pattern-style' : pwdBox.code.pattern}"  placeholder="" /><button class="getCode" ref="yzm" @click="getCode($event)"></button></div>
                        <div class="newPwd"><span class="text"></span><el-input v-model="pwdBox.newPwd.value" :class="{'pattern-style' : pwdBox.newPwd.pattern}" maxlength="11" placeholder="6-11" show-password></el-input></div>
                        <div class="newPwds"><span class="text"></span><el-input v-model="pwdBox.RepeatPwd.value" :class="{'pattern-style' : pwdBox.RepeatPwd.pattern}" maxlength="11" placeholder="6-11"  show-password></el-input></div>
                        <button class="save" @click="updatePwd"></button>
                    </form>
                </div>
            </el-popover>
        </div>
        <div class="modify_phone">
             <div class="phoneBox"  @click="showPhone" ref="phoneBox">
                <img src="../../../assets/my/phone.png"/>
                <p>1527923****</p>
                <span></span>
            </div>
            <el-popover
                placement="bottom"
                trigger="click"
                visible-arrow="true"
                v-model="visible_phone">
                <div class="phone_modifyBox">
                    <form>
                        <h6>*</h6>
                        <div class="newPwd"><span class="text"></span><el-input v-model="phoneBox.pwd.value" ref="pwd" :class="{'pattern-style' : phoneBox.pwd.pattern}" maxlength="11" placeholder="6-11" show-password></el-input></div>
                        <div class="phoneNum"><span class="text"></span><input v-model="phoneBox.phone.value" ref="phone"  :class="{'pattern-style' : phoneBox.phone.pattern}" class="input" maxlength="11" placeholder="" /></div>
                        <div class="phoneCode"><span class="text"></span><input v-model="phoneBox.code.value" ref="code" :class="{'pattern-style' : phoneBox.code.pattern}" class="input"  placeholder="" /><button></button></div>
                        <button class="save"  @click="updatePhone"></button>
                    </form>
                </div>
            </el-popover>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            visible_pwd:true,
            visible_phone:false,
            pwdBox:{
                phone:{
                    value:'',
                    pattern:false,
                },
                code:{
                    value:'',
                    pattern:false,
                },
                newPwd:{
                    value:'',
                    pattern:false,
                },
                RepeatPwd:{
                    value:'',
                    pattern:false
                },
            },
            phoneBox:{
                pwd:{
                    value:'',
                    pattern:false,
                },
                phone:{
                    value:'',
                    pattern:false
                },
                code:{
                    value:'',
                    pattern:false,
                },
            }
           
        }
    },
    methods:{
        showPwd(){
            this.visible_pwd = true;
            this.visible_phone = false;
            if( this.visible_phone == false){
                 this.$refs.phoneBox.style.border="1px solid";
            }
           this.$refs.pwdBox.style.border="1px solid #28ffd4";
        },
        showPhone(){
            this.visible_phone = true;
            this.visible_pwd = false;
            if( this.visible_pwd == false){
                 this.$refs.pwdBox.style.border="1px solid";
            }
           this.$refs.phoneBox.style.border="1px solid #28ffd4";
        },
        getCode(){
           var yzm = this.$refs.yzm;
           let second = 60;
           let timer =  setInterval (()=>{
                second --;
                yzm.disabled = true;
                yzm.innerHTML = second + 's';
                if(second == 0 ){
                    yzm.innerHTML = '';
                    yzm.disabled = false;
                    clearInterval(timer);
                }
           },1000)
          
        } ,
        updatePwd(){
            this.$store.commit('MODIFY_PWD',{})
        },
        updatePhone(){
            
            if(this.phoneBox.pwd.value && (/^(?=.{6,11})(((?=.*[A-Z])(?=.*[a-z]))|bai((?=.*[A-Z])(?=.*[0-9]))|((?=.*[a-z])(?=.*[0-9]))).*$/.test(this.phoneBox.pwd.value))){
                this.phoneBox.pwd.pattern = true;
                this.$refs.pwd.focus();
                console.log('')
            }else{
                return false;
            }
            if( this.phoneBox.phone.value && (/^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(this.phoneBox.phone.value))){
                this.$refs.phone.focus();
                this.phoneBox.phone.pattern = true;
                console.log('')
            }else{
                // console.log('')
                return false;
            }

        }
    }
}
</script>
<style  lang='less' scoped>
  @import url('../../../assets/style.less');
.user-my-mySetting{
    padding:40px 0 0 60px;
    display: flex;
     /deep/.el-popover.el-popper{
        margin:30px 0;
        height: 400px;
        background:#141926;
        border:1px solid @color-select;
        width:750px !important;
     }
     //
     .pwd_modifyBox,.phone_modifyBox{
        form{
            flex-direction: column;
            display: flex;
            align-items: center;
            input::-webkit-input-placeholder { /* WebKit browsers */
                color:#c0c4cc;
                font-size: 16px;
            }
            input.pattern-style::-webkit-input-placeholder {
                color: red;
            }
            
           h6{
            text-align: center;
            font-size: 14px;
            color:@color-tip;
           }
           div{
               display: flex;
               align-items: center;
               width:380px;
               margin:15px 0 10px 0;
               .text{
                   font-size: 18px;
                   display: inline-block;
                   width:75px;
                   text-align: right;
                   margin:0 15px 0 0;
                   color:@color-used;
               }
               .input{
                   padding-left: 15px;
                   width:230px;
                   outline: none;
                   height: 36px;
                   color:@color-tip;
                   font-size: 16px;
                   background:none;
                   border:1px solid @color-two-level;
               }
           }
           .phoneCode{
               input{
                   width:125px;
                   margin-right: 10px;
               }
               button{
                   outline: none;
                   font-size: 14px;
                   color:@color-used;
                   width:90px;
                   height: 38px;
               }
           }
           .newPwd,.newPwds{
               height: 40px;
               /deep/.el-input__inner{
                    background:none;
                    border:none;
                    font-size: 16px;
                    color:@color-tip;
               }
               /deep/.el-input.el-input--suffix{
                   width:245px;
                   border:1px solid @color-two-level;
               }
           }
            .save{
               width: 118px;
                height: 34px;
                outline: none;
                border: none;
                margin: 30px 0;
                text-align: center;
                font-size: 14px;
                color: #fff;
                background: linear-gradient(#07e2c6, #069ee7);
           }
        }
     }  
     //
    .modify_pwd{ 
        margin:0 30px 0 0;
        .pwdBox{
            display: flex;
            flex-direction: column;
            width: 116px;
            height: 78px;
            background:#141926;
            padding:10px 20px 20px 20px;
            border:1px solid @color-select;
            img{
                width:30px;
                height:30px;
                align-self: center;
            }
            span{
                font-size: 16px;
                color:#fff;
                margin:30px 0 0 0;
                text-align: center;
            }
        }
       .pwd_modifyBox{
           padding:20px 80px 0 80px; 
           .phoneNum{
               margin-top:30px;
           }
       }
       .pwd_modifyBox:before{
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            left: 8%;
            bottom: 100%;
            border: solid transparent ;
            border-width: 13px;
            border-bottom-color: @color-select;
        }
    }
    //
    .modify_phone{
        .phoneBox{
            display: flex;
            flex-direction: column;
            width: 116px;
            height: 78px;
            background:#141926;
            padding:10px 20px 20px 20px;
            border:1px solid #141926;
            img{
                width:30px;
                height:30px;
                align-self: center;
            }
            p{
                font-size: 14px;
                color:@color-tip;
                text-align: center;
                margin:8px 0;
            }
            span{
                font-size: 16px;
                color:#fff;
                text-align: center;
            }
        }
         /deep/.el-popover.el-popper{
            position: relative;
            left:-188px;
        }
        .phone_modifyBox{
            padding:30px 50px;
            div{
                margin:15px 0 15px 0;
            }
            .newPwd{
                margin-top: 30px;
            }
            .save{
                margin:40px 0 0 0;
            }
        }
        .phone_modifyBox:before{
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            left: 33%;
            bottom: 100%;
            border: solid transparent ;
            border-width: 13px;
            border-bottom-color: @color-select;
        }
    }
}
</style>