// 
// import utils from '../../../utils/common.js';
export const MODIFY_INFO = 'MODIFY_INFO'
export default {
    state: {
        result: {},
    },
    getters: {

    },
    mutations: {
        [MODIFY_INFO](state, data) {
            if (data) {
                state.result = data

            }
        },
    },
    actions: {
        [MODIFY_INFO]({ commit }, data) {
            commit(MODIFY_INFO, data)
        }
    }
}