<template>
    <div class="component-nav">
        <div class="nav">
            <div class="row">
                <div class="logo">
                    <router-link :to="{ path:'/home/match' }">
                        <img src="./../assets/common/logo.png" />
                    </router-link>
                </div>
                <div class="block">
                    <ul v-if="$route.name != '/register' && $route.name != '/login' && $route.name != '/retrievePassword'">
                        <li><router-link to="/home/match"></router-link></li>
                        <li><router-link to="/shop/goods"></router-link></li>
                        <li><router-link to="/user/active"></router-link></li>
                        <li><router-link to="/user/invite/myInvite"></router-link></li>
                    </ul>
                </div>
                <div class="personal">
                    <div v-if="(!gameUserInfo) && ($route.name != '/register' && $route.name != '/login' && $route.name != '/retrievePassword')" class="vip"><img src="./../assets/common/vip.png" /></div>
                    <div v-if="!gameUserInfo" class="noLogin">
                        <el-button class="register" @click="register">
                            
                        </el-button>
                        <el-button class="login" @click="login">
                            
                        </el-button>
                    </div>
                    <div v-if="gameUserInfo" class="loginInfo">
                        <div class="text"><router-link to="/user/my/myInfo"></router-link></div>
                        <div class="menu">
                            <el-dropdown trigger="click">
                                <span class="btn">
                                    <img class="avatar" src="./../assets/common/header.png" />
                                    <i class="el-icon-caret-bottom el-icon--right"></i>
                                </span>
                                <el-dropdown-menu class="nav-menu-list" slot="dropdown">
                                    <el-dropdown-item></el-dropdown-item>
                                    <el-dropdown-item>
                                        <div>888,888</div>
                                        <p></p>
                                    </el-dropdown-item>
                                    <el-dropdown-item>
                                        <div>888,888</div>
                                        <p></p>
                                    </el-dropdown-item>
                                    <router-link style="color:#fff" to="/user/wallet/recharge"><el-dropdown-item divided><img src="./../assets/common/price.png" /> </el-dropdown-item></router-link>
                                    <router-link style="color:#fff" to="/user/wallet/exchange"><el-dropdown-item><img src="./../assets/common/transfer.png" /> </el-dropdown-item></router-link>
                                    <router-link style="color:#fff"  to="/user/news/systemMsg"><el-dropdown-item><img src="./../assets/common/message.png" /> </el-dropdown-item></router-link>
                                    <el-dropdown-item divided>
                                        <div @click="logOut"><img src="./../assets/common/logout.png" /> </div>
                                    </el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default{
    data(){
        return {
            gameUserInfo: null
        }
    },
    methods:{
        register(){
            this.$router.push('/register')
        },
        login(){
            this.$router.push('/login')
        },
        logOut() {
            sessionStorage.clear();
            this.$router.push('/login');
        }
    },
    mounted() {
        
    },
    watch:{
        $route(){
            console.log(this.utils.gameUserInfo());
            let gameUserInfo = this.utils.gameUserInfo();
            if (JSON.stringify(gameUserInfo) != '{}') {
                console.log(gameUserInfo);
                this.gameUserInfo = gameUserInfo;
            } else {
                this.gameUserInfo = null;
            }
        }
    }
}
</script>
<style lang="less">
@import url('../assets/style.less');
.component-nav {
    .nav {
        background-color: #090C15;
        .row {
            display: flex;
            width: 1246px;
            height: 80px;
            margin: 0 auto;
            align-items: center;
            .logo {
                width: 310px;
                img {
                    max-width: 200px;
                    display: inline-block;
                    vertical-align: middle;
                }
            }
            .block {
                flex: 1;
                color: #7086aa;
                font-size: 18px;
                ul {
                    list-style: none;
                    li {
                        cursor: pointer;
                        padding: 15px 0 15px 0;
                        margin: 0 78px 0 0;
                        display: inline-block;
                    }
                    .router-link-active{
                        color:#fff;
                        font-weight: bold;
                    }
                }
            }
        }
        .personal {
            display: flex;
            align-items: center;


            .noLogin {
                .register, .login {
                    border-width: 0;
                    height: 34px;
                    width: 90px;
                    padding: 0;
                    color: #FFFFFF;
                    
                }
                .register {
                    background-color: #1A2335;
                    &:active {
                        background: rgba(26, 35, 53, 0.8);
                    }
                }
                .login {
                    background-color: #26B1BF;
                    &:active {
                        background: rgba(38, 177, 191, 0.8);
                    }
                }
            }
            .vip {
                margin: 0 55px 0 0;
            }
            .loginInfo {
                display: flex;
                align-items: center;
                img.avatar {
                    width: 46px;
                    vertical-align: middle;
                    border-radius: 50%;
                    padding: 0 0 0 8px;
                }
                .text {
                    font-size: 18px;
                    margin: 0 20px 0 0;
                    a{
                        color: #07e9f1;
                    }
                }
                .menu {
                    .btn {
                        color: #07e9f1;
                    }
                }
            }
        }
    }
}
</style>
